import React, { ChangeEvent } from 'react';
import ReactDOM from 'react-dom';
import { BotOrPlayersRadio, DifficultyRadio } from './ChoiceElements'
import { Game } from './GameElement'
import './style.css'


interface AppPropsType {
  name: string
}

interface AppStatesType {
  twoPlayersChecked: boolean,
  againstBotChecked: boolean,
  gameInProgress: boolean,
  randomChecked: boolean,
  perfectChecked: boolean
}



class App extends React.Component<AppPropsType, AppStatesType> {
  constructor(props: AppPropsType) {
    super(props)
    this.state = {
      twoPlayersChecked: true,
      againstBotChecked: false,
      gameInProgress: false,
      randomChecked: true,
      perfectChecked: false
    }

    this.handleOpponentTypeChange = this.handleOpponentTypeChange.bind(this)
    this.handleDifficultyChange = this.handleDifficultyChange.bind(this)
    this.startMatch = this.startMatch.bind(this)
    this.endGame = this.endGame.bind(this)
  }

  handleOpponentTypeChange(event: ChangeEvent<HTMLInputElement>) {
    if (event.target.value === 'bot') {
      this.setState({ againstBotChecked: true, twoPlayersChecked: false })
    } else if (event.target.value === 'twoPlayers') {
      this.setState({ againstBotChecked: false, twoPlayersChecked: true })
    }
  }

  handleDifficultyChange(event: ChangeEvent<HTMLInputElement>) {
    if (event.target.value === 'random') {
      this.setState({ randomChecked: true, perfectChecked: false })
    } else if (event.target.value === 'perfect') {
      this.setState({ randomChecked: false, perfectChecked: true })
    }
  }

  startMatch() {
    this.setState({
      gameInProgress: true
    })
  }

  endGame() {
    this.setState({
      gameInProgress: false
    })
  }

  render() {
    return (
      <div>
        <h1>{this.props.name}</h1>
        <div id='choices'>
          <BotOrPlayersRadio disabled={this.state.gameInProgress} twoPlayersChecked={this.state.twoPlayersChecked} againstBotChecked={this.state.againstBotChecked} handleChange={this.handleOpponentTypeChange} />
          {this.state.againstBotChecked && <DifficultyRadio disabled={this.state.gameInProgress} randomChecked={this.state.randomChecked} perfectChecked={this.state.perfectChecked} handleChange={this.handleDifficultyChange} />}
          <br />
        </div>
        <button id='startButton' onClick={this.startMatch} disabled={this.state.gameInProgress}>Start Game!</button>
        {this.state.gameInProgress && <Game endCallback={this.endGame} opponentType={this.state.againstBotChecked ? 'bot' : 'player'} ai={this.state.againstBotChecked ? this.state.randomChecked ? 'random' : 'perfect' : undefined} />}
      </div>
    )
  }
}

ReactDOM.render(
  <App name='Tic Tac Toe' />,
  document.getElementById('root')
);
