import React from "react"
import { getBestMove } from './getBestMove'

interface GameStatesType {
    winPattern?: [number, number, number],
    isBotTurn: boolean,
    gameEnded: boolean,
    XsTurn: boolean,
    boardState: [('X' | 'O' | '-'), ('X' | 'O' | '-'), ('X' | 'O' | '-'), ('X' | 'O' | '-'), ('X' | 'O' | '-'), ('X' | 'O' | '-'), ('X' | 'O' | '-'), ('X' | 'O' | '-'), ('X' | 'O' | '-')]
}

interface GamePropsType {
    opponentType: 'player' | 'bot',
    ai?: 'perfect' | 'random',
    endCallback: CallableFunction
}

class Game extends React.Component<GamePropsType, GameStatesType> {
    constructor(props: GamePropsType) {
        super(props)
        this.state = {
            gameEnded: false,
            isBotTurn: false,
            XsTurn: true,
            boardState: ['-', '-', '-', '-', '-', '-', '-', '-', '-'],
            winPattern: undefined
        }

        this.reset = this.reset.bind(this)
        this.play = this.play.bind(this)
        this.handleClick = this.handleClick.bind(this)
        this.checkWin = this.checkWin.bind(this)
        this.botPlay = this.botPlay.bind(this)
        this.makeplay = this.makeplay.bind(this)
    }

    componentDidMount() {
        this.reset()
    }

    reset() {
        this.setState({
            winPattern: undefined,
            XsTurn: true,
            boardState: ['-', '-', '-', '-', '-', '-', '-', '-', '-'],
            gameEnded: false
        })
    }

    checkWin(futureBoardState: ('X' | 'O' | '-')[]) {
        const b = futureBoardState
        const winConditions = [[0, 1, 2], [3, 4, 5], [6, 7, 8], [0, 3, 6], [1, 4, 7], [2, 5, 8], [0, 4, 8], [2, 4, 6]]
        for (let wc of winConditions) {
            if (b[wc[0]] === b[wc[1]] && b[wc[0]] === b[wc[2]] && b[wc[0]] !== '-') {
                return wc
            }
        }
        return false
    }
    makeplay(p: 'X' | 'O', i: number, prevState: Readonly<GameStatesType>) {
        //@ts-ignore
        const futureBoardState = prevState.boardState.slice(0, i).concat([p].concat(prevState.boardState.slice(i + 1,)))

        if (futureBoardState.indexOf('-') === -1 && !this.checkWin(futureBoardState)) {
            setTimeout(this.props.endCallback, 2500)
            return {
                boardState: futureBoardState,
                gameEnded: true,
                winPattern: undefined
            }
        }
        if (this.checkWin(futureBoardState)) {
            setTimeout(this.props.endCallback, 2500)
            return {
                boardState: futureBoardState,
                gameEnded: true,
                winPattern: this.checkWin(futureBoardState)
            }
        }
        return {
            boardState: futureBoardState,
            XsTurn: p === 'O' ? true : false
        }
    }

    play(i: number) {
        if (this.state.XsTurn) {
            //@ts-ignore
            this.setState((prevState) => {
                return this.makeplay('X', i, prevState);
            })
        } else {
            //@ts-ignore
            this.setState((prevState) => {
                return this.makeplay('O', i, prevState)
            })
        }

    }

    botPlay() {
        this.setState({ isBotTurn: true })
        setTimeout(() => {
            if (this.state.gameEnded || this.state.boardState.indexOf('-') === -1) return;
            if (this.props.ai === 'random') {
                let a = Math.floor(Math.random() * 9)
                while (this.state.boardState[a] !== '-') {
                    a = Math.floor(Math.random() * 9)
                }
                this.play(a)
                this.setState({ isBotTurn: false })
            } else {
                let a = getBestMove(this.state.boardState)
                this.play(a)
                this.setState({ isBotTurn: false })
            }
        }, 1000)
    }

    handleClick(event: React.MouseEvent<HTMLButtonElement>) {
        //@ts-ignore
        if (this.state.boardState[Number(event.target.id)] === '-') {
            //@ts-ignore
            this.play(Number(event.target.id))
            if (this.props.opponentType === 'bot' && (!this.state.gameEnded || this.state.boardState.indexOf('-') !== -1)) {
                this.botPlay()
            }
        }
    }

    render() {
        let board = this.state.boardState.map((x, i) => <button disabled={this.state.gameEnded || this.state.isBotTurn} id={String(i)} onClick={this.handleClick} key={i} style={{ width: 100, height: 100, fontSize: 40, color: "whitesmoke", fontFamily: 'Arial',backgroundColor: this.state.winPattern ? this.state.winPattern.indexOf(i) !== -1 ? 'green' : '#333333' : '#333333' }}>{this.state.boardState[i] === '-' ? '' : this.state.boardState[i] === 'X' ? 'X' : 'O'}</button>)
        return (<div id='board'>
            {!this.state.gameEnded && <h3>It is <span id='turnSpan'>{this.state.XsTurn ? 'X' : 'O'}</span>'s Turn!</h3>}
            {this.state.gameEnded && <h3 style={{fontSize:30}}>{`${this.state.winPattern ? this.state.boardState[this.state.winPattern[0]] : 'No one'} Won!`}</h3>}
            <div style={{ margin: 'auto',display: "grid", gridTemplateColumns: 'repeat(3, 1fr)', rowGap: 1, columnGap: 1, width: 305, height: 305 }}>{board}</div>
        </div>)
    }

}

export { Game }