import { ChangeEventHandler } from "react"

interface OpponentProps {
    disabled: boolean,
    twoPlayersChecked: boolean,
    againstBotChecked: boolean,
    handleChange: ChangeEventHandler<HTMLInputElement>
}

interface DifficultyProps {
    disabled: boolean,
    randomChecked: boolean,
    perfectChecked: boolean,
    handleChange: ChangeEventHandler<HTMLInputElement>
}

const BotOrPlayersRadio = (props: OpponentProps) => {
    return (<div>
        <p>Opponent Type:</p>
        <input type='radio' disabled={props.disabled} checked={props.twoPlayersChecked} value='twoPlayers' name='opponentType' id='twoPlayers' onChange={props.handleChange} />
        <label htmlFor='twoPlayers'>vs Player  </label>
        <input type='radio' disabled={props.disabled} checked={props.againstBotChecked} value='bot' name='opponentType' id='bot' onChange={props.handleChange} />
        <label htmlFor='bot'>vs Bot</label>
    </div>)
}

const DifficultyRadio = (props: DifficultyProps) => {
    return (<div>
        <p>Bot strategy:</p>
        <input type='radio' disabled={props.disabled} checked={props.randomChecked} value='random' name='gameDifficulty' id='random' onChange={props.handleChange} />
        <label htmlFor='random'>Random Choices  </label>
        <input type='radio' disabled={props.disabled} checked={props.perfectChecked} value='perfect' name='gameDifficulty' id='perfect' onChange={props.handleChange} />
        <label htmlFor='perfect'>Perfect</label>
    </div>)
}
export { BotOrPlayersRadio, DifficultyRadio };